.App {
  display: flex;
  text-align: center;
  align-items: center;
  flex-direction: column;
}

.sheet {
  width: 60rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 3rem;
  margin-top: 3rem;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.box {
  margin: 15px;
  display: flex;
  flex-direction: column;
  width: 40rem;
  justify-content: center;
  align-items: center;
}

.mr10 {

  margin-top: 2rem;
  border: none;
  width: 30rem;
}

.mr10::placeholder {
  text-align: left;
}

input {
  padding: 5px 10px;
}

button {
  padding: 5px 10px;
  margin-right: 10px;
}

.print {
  margin-top: 20px;
}

.formBtn {
  margin-top: 1rem;
  border-radius: 5px;
}

.print {
  margin-bottom: 1rem;
}
